import { BrowserRouter, Switch, Route, Link } from "react-router-dom";
import "./styles.css";

const Animales = [
  { type: "dog", id: "123" },
  { type: "cat", id: "1234" }
];

const Navbar = () => {
  return (
    <ul>
      <li>
        <Link to="/">Home</Link>
      </li>
      <li>
        <Link to="/miausmiaus/1234">Miaumiaus</Link>
      </li>
      <li>
        <Link to="/guauguaus/marlon">Guauguaus</Link>
      </li>
    </ul>
  );
};

const Header = () => <p>Tenemos</p>;

function HomeScreen() {
  return <Header />;
}

function Miaumiaus(props) {
  const { gatosId } = props.match.params;
  console.log(Animales.filter((animal) => animal.id == gatosId));
  return (
    <>
      {" "}
      <Header /> <span>Tenemos muchos gatitos</span>
    </>
  );
}

function Guauguaus(props) {
  const { idPerro } = props.match.params;
  console.log(idPerro);
  return (
    <>
      {" "}
      <Header />
      <span>Tenemos muchos perritos</span>
    </>
  );
}

function NoMathScreen() {
  return <span>Error page, not found 404</span>;
}

export default function App() {
  return (
    <BrowserRouter>
      <Navbar />
      <hr />
      <Switch>
        <Route exact path="/" component={HomeScreen} />
        <Route path="/miausmiaus/:gatosId" component={Miaumiaus} />
        <Route exact path="/Guauguaus/:idPerro" component={Guauguaus} />
        <Route path="*" component={NoMathScreen} />
      </Switch>
      <hr />
      <footer>
        <span>Hola soy un pie de p√°gina</span>
      </footer>
    </BrowserRouter>
  );
}
